.running-row {
  white-space: nowrap;
  overflow: hidden; }
  .running-row__item {
    position: relative;
    display: inline-block;
    margin-right: 37px;
    font-family: "Sofia Pro Bold", sans-serif;
    font-weight: 400;
    font-size: 36px;
    line-height: 36px;
    text-transform: uppercase;
    opacity: 0.5;
    text-shadow: 0px 1px 0 black, 0px -1px 0 black, 1px 0px 0 black, -1px 0px 0 black, 1px 1px 0 black, 1px -1px 0 black, -1px 1px 0 black, -1px -1px 0 black;
    -webkit-text-shadow: 0px 1px 0 black, 0px -1px 0 black, 1px 0px 0 black, -1px 0px 0 black, 1px 1px 0 black, 1px -1px 0 black, -1px 1px 0 black, -1px -1px 0 black;
    -moz-text-shadow: 0px 1px 0 black, 0px -1px 0 black, 1px 0px 0 black, -1px 0px 0 black, 1px 1px 0 black, 1px -1px 0 black, -1px 1px 0 black, -1px -1px 0 black; }
    .running-row__item--white {
      color: white; }
    .running-row__item--beige {
      color: #dedcd4; }
    .running-row__item::after {
      content: '';
      position: absolute;
      right: -27px;
      top: 50%;
      transform: translateY(-50%);
      width: 11px;
      height: 11px;
      border-radius: 100%;
      background-color: transparent;
      border: 1px solid rgba(0, 0, 0, 0.8); }

.running-row .active {
  color: #C65B0C;
  opacity: 1;
  text-shadow: none;
  -webkit-text-shadow: none;
  -moz-text-shadow: none; }

