.sidebar {
  height: 100vh;
  width: 19%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 92px 40px 80px;
  position: fixed;
  z-index: 10; }
  .sidebar-nav {
    display: flex;
    flex-direction: column; }
    .sidebar-nav__link {
      font-family: "Sofia Pro Regular", sans-serif;
      font-weight: 400;
      font-size: 31px;
      line-height: 31px;
      color: #000000;
      margin-bottom: 6px; }
  .sidebar-logo__link {
    font-family: "Sofia Pro Bold", sans-serif;
    font-size: 24px;
    line-height: 24px;
    letter-spacing: 0.025em;
    text-transform: uppercase;
    text-decoration: none;
    color: black; }

.hero {
  padding: 70px 0 80px;
  position: relative;
  height: calc(100vh + 64px); }
  .hero__container {
    padding-right: 40px; }
  .hero__title {
    position: absolute;
    left: 0;
    top: 250px;
    font-family: mr_ArenqG, sans-serif;
    font-size: 440px;
    color: #FFFFFF;
    opacity: 0.5;
    font-weight: 400;
    line-height: 450px;
    z-index: -1; }
    .hero__title::after {
      content: '';
      position: absolute;
      right: -53px;
      bottom: 113px;
      width: 50px;
      height: 50px;
      background-color: transparent;
      border: 4px solid #FFFFFF;
      border-radius: 100%; }
  .hero__subtitle {
    position: relative;
    font-family: "Sofia Pro Regular", sans-serif;
    font-size: 180px;
    line-height: 81.5%;
    letter-spacing: -8px;
    font-weight: 400;
    margin-bottom: 300px; }
  .hero__text {
    max-width: 683px;
    font-size: 24px;
    line-height: 140%;
    margin-bottom: 30px; }
  .hero-social {
    display: flex;
    justify-content: flex-end; }
    .hero-social__link {
      font-family: "Sofia Pro Regular", sans-serif;
      font-size: 18px;
      line-height: 18px;
      color: black;
      text-transform: uppercase; }
      .hero-social__link:not(:last-child) {
        margin-right: 30px; }
  .hero-link {
    display: inline-block;
    padding: 12px 52px;
    text-transform: uppercase;
    text-decoration: none;
    color: black;
    background-color: transparent;
    border: 1.5px solid #292828; }
  .hero__link {
    margin-bottom: 35px; }

.projects {
  width: 100%;
  min-height: 961px;
  padding: 0.1px 0.1px 77px 0.1px; }
  .projects__container {
    position: relative; }
  .projects__title {
    position: absolute;
    top: -64px;
    left: -10px; }
  .projects__image {
    width: 100%;
    margin-bottom: 26px; }
  .projects-cards {
    display: flex;
    flex-wrap: wrap;
    max-width: 1126px;
    padding-right: 10px; }
  .projects__cards {
    margin-bottom: 80px; }
  .projects-card {
    width: 100%;
    max-width: 537px; }
    .projects-card:first-child {
      max-width: 100%;
      margin-bottom: 100px; }
    .projects-card:nth-child(2) {
      margin-top: -150px; }
    .projects-card:nth-child(3) {
      margin-bottom: 100px; }
    .projects-card:nth-child(4) {
      margin-top: -150px; }
    .projects-card:nth-child(2n) {
      margin-right: auto; }
    .projects-card__image {
      margin-bottom: 28px; }
    .projects-card__title {
      font-family: "Sofia Pro Bold", sans-serif;
      font-weight: 400;
      font-size: 24px;
      line-height: 24px;
      text-transform: uppercase;
      letter-spacing: 0.6px;
      color: #C65B0C; }
    .projects-card__subtitle {
      font-family: "Sofia Pro Regular", sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 16px;
      text-transform: uppercase;
      letter-spacing: 0.4px;
      color: #292828; }
    .projects-card__info {
      font-family: "Sofia Pro Regular", sans-serif;
      font-weight: 400;
      font-size: 11px;
      line-height: 12px;
      letter-spacing: -0.1px;
      color: #292828;
      opacity: 0.5;
      text-transform: uppercase; }
    .projects-card-content {
      position: relative;
      max-width: 537px;
      padding: 10px 30px 10px 0; }
      .projects-card-content:first-child {
        margin-left: auto; }
        .projects-card-content:first-child::after {
          left: 0; }
      .projects-card-content::after {
        content: '';
        position: absolute;
        bottom: 0;
        width: 100%;
        max-width: 537px;
        height: 1px;
        left: 0;
        background-color: #292828;
        opacity: 0.4; }
      .projects-card-content__row {
        display: flex;
        justify-content: space-between;
        align-items: flex-end; }
        .projects-card-content__row:first-child {
          margin-bottom: 17px; }
        .projects-card-content__row:last-child {
          margin-bottom: 38px; }

.about {
  background-color: #fff;
  padding: 44px 0 77px; }
  .about__container {
