    top: calc(100vw / 7);
    font-size: calc(100vw / 3.2); }
    .hero__title::after {
      display: none;
      right: -53px; }
  .hero__subtitle {
    font-size: calc(100vw / 8);
    margin-bottom: calc(100vw/4.2); }
  .projects-card {
    max-width: 450px; }
